.dropdown-content {
  display: none;
  position: absolute;
  right: 5px;
  background-color: #f9f9f9;
  min-width: 100px;
  z-index: 1;
  margin-top: 150px;
}
.dropdown-content a {
  color: black;
  padding: 2px;
  text-decoration: none;
  display: block;
}
